.loadImage {
    display: flex;
    align-items: center;
    cursor: pointer;

    .title-button {
        cursor: pointer;
        font-size: 16px;
        padding: 20px;
    }
    @media (min-width:768px) {
        .title-button {
            padding: 15px;
            font-size: 25px;
        }
    }

    /* web styles */
    --b: 3px;
    /* border thickness */
    --s: .15em;
    /* size of the corner */
    --c: #BD5532;

    padding: calc(.05em + var(--s)) calc(.3em + var(--s));
    color: var(--c);
    --_p: var(--s);
    background: conic-gradient(from 90deg at var(--b) var(--b), #0000 90deg, var(--c) 0) var(--_p) var(--_p)/calc(100% - var(--b) - 2*var(--_p)) calc(100% - var(--b) - 2*var(--_p));
    transition: .3s linear,
    color 0s,
    background-color 0s;
    outline: var(--b) solid #0000;
    outline-offset: .2em;

    font-family: system-ui,
    sans-serif;
    font-weight: bold;
    font-size: 4rem;
    cursor: pointer;
    border: none;
    margin: .1em;


}

button:hover,
button:focus-visible {
    --_p: 0px;
    outline-color: var(--c);
    outline-offset: .05em;
}

button:active {
    background: var(--c);
    color: #fff;
}